.header-cabinet {
  background: #000;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}

.header__profile {
  display: flex;
  align-items: center;
}

.header__profile-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  object-position: top;
}

.header__nav {
  background-color: #292929;
  max-height: 72px;
  color: #fff;
}
.header__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  height: 100%;
  width: 100%;
}

.header__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  width: 100%;
  position: relative;
}

.header__item svg {
  margin-right: 10px;
}

.header__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: 100%;
  position: relative;
  transition: all 0.3s linear;
  width: 100%;
  font-size: 16px;
}

.header__link svg path {
  transition: all 0.3s linear;
}

.header__link:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  transition: all 0.3s linear;
  background: transparent;
}

.header__link:hover {
  color: #fce34d;
  position: relative;
}

.header__link:hover svg path {
  stroke: #fce34d;
}

.header__link:hover {
  color: #fce34d;
  position: relative;
}

.header__link--active {
  color: #fce34d;
}

.header__link--active::before {
  background-color: #fce34d;
}

.header__link--active svg path {
  stroke: #fce34d;
}
@media (max-width: 768px) {
  .header__profile-avatar {
    width: 50px;
    height: 50px;
  }
}
